// 对Vue全局指令的封装
export default {
  install(Vue) {
    // inserted指令所在标签被插入到DOM时触发，标签隐藏再出现不会触发
    Vue.directive('fofo', {
      inserted(el) {
        if (el.nodeName === 'TEXTAREA' || el.nodeName === 'INPUT') {
          el.focus()
        } else {
          if (el.querySelector('input')) el.querySelector('input').focus()
          if (el.querySelector('textarea')) el.querySelector('textarea').focus()
        }
      },
      update(el) {
        if (el.nodeName === 'TEXTAREA' || el.nodeName === 'INPUT') {
          el.focus()
        } else {
          if (el.querySelector('input')) el.querySelector('input').focus()
          if (el.querySelector('textarea')) el.querySelector('textarea').focus()
        }
      }
    })
  }
}
