import Vue from 'vue'
import Vuex from 'vuex'
import { getItem, setItem } from '@/utils/storage'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 一个对象，用来存储当前用户信息（token等数据）
    user: getItem('TOKEN_KEY'),
    avatar: getItem('avatar'),
    uname: ''
  },
  getters: {},
  mutations: {
    // 建议最好大写
    setUser(state, val) {
      if (typeof val === 'string') {
        state.user.token = val
      } else {
        state.user = val
      }
      // 为了方式刷新丢失，存loalStorage,持久化
      setItem('TOKEN_KEY', state.user)
    },
    setAvatar(state, val) {
      state.avatar = val
      setItem('avatar', val)
    },
    SETUNAME(state, val) {
      state.uname = val
    }
  },
  actions: {},
  modules: {}
})
